export default function MyBunsyo() {
  return (
    <div>
      <p>
        [解説]
        <br />
        ・テツノカイナ
        <br />
        レイドバトル要員その1。バイオレット限定ポケモン。腹太鼓ドレパンで格闘等倍以上のタイプに使っていける汎用性最強。レイドにおいてはスカーレットの禁伝枠であるコライドンよりも明らかに強い。バイオレット限定なのでスカーレット民はバイオレット民に土下座をしてでも譲ってもらうことをオススメする。エレキフィールドは特性発動と眠り対策目的で積んでいるが、剣舞や他の攻撃技でもいいかなと思ってる。
        <br />
        <br />
        ・ソウブレイズ
        <br />
        レイドバトル要員その2。バイオレット限定ポケモン。テツノカイナが使えない格闘半減以下などに使える補完枠。テツノカイナほどの火力は出せないが、剣舞無念の剣で似たような動きができるためレイドではかなり強い。積み技対策のクリアスモッグもあり無限戦術ができる。炎タイプのため素で火傷無効なのも地味に優秀。相手によってはクリアスモッグを挑発に変えたほうがいい。
        <br />
        <br />
        ・マリルリ
        <br />
        レイドバトル要員その3。ドレパンや無念の剣のような吸収技は覚えないが、レイドは敵のHPが非常に多いため貝殻の鈴でも意外と回復できる。雨乞い腹太鼓アクアブレイクでワンパンも狙えるので、その場合はオボンがオススメ。個人的にはもうすぐ来る最強リザードンイベントでの最適解になるんじゃないかと思ってる。もしサンパワーソーラービームとかまでしてくるなら誰も倒せないやろ。ちなみに今のレイドではじゃれつくがバグっていて使えなかったりする。
        <br />
        <br />
        ・テツノドクガ
        <br />
        レイドバトル要員その4。バイオレット限定ポケモン。妖対策枠。アシッドボムによって確定で相手の特防を二段階下げることができる。命中100かつ攻撃技であるという点が優秀で、外しのリスクがなく、テラスタルオーブのチャージも貯められる。吸収技は覚えないが回復技は覚えられる。蝶の舞使えなくて笑った。
        <br />
        <br />
        ・ハバタクカミ
        <br />
        金策要員。スカーレット限定ポケモン。こいつ一匹のパーティなら学園最強大会をAボタン連打で脳死周回することができるので、連射コントローラーを買えば放置周回で金に困ることがなくなる。連コンはHoriのジョイコン型のやつがオススメ。注意点として、最初の三匹でニャオハを選んでしまったプレイヤーはハバタクカミが校長先生のラウドボーンに倒されてしまうことがあるので周回に使うことができない。その場合は夢ニンフィアがオススメ。
        <br />
        <br />
        ・テツノブジン
        <br />
        捕獲要員。バイオレット限定ポケモン。催眠術と峰打ちを両立できるポケモンの中で一番攻撃が高い。キノガッサで茸の胞子派の人の方が多いと思うが、霊だけでなく草を捕まえるときまでいちいちペリッパーなどで水浸しを使うのが面倒なので、基本こいつで霊だけハバタクカミのナイトヘッド使ってる。
        <br />
        <br />
        <br />
        [最後に]
        <br />
        レイド難しすぎる。
      </p>
    </div>
  );
}
